@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaBT-Book.woff2") format("woff2"), url("../font/FuturaBT-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaCyrillic-Demi.woff2") format("woff2"), url("../font/FuturaCyrillic-Demi.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaBT-Medium.woff2") format("woff2"), url("../font/FuturaBT-Medium.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
:root {
  --pc: #E21E24;
  --sc:#667085;
  --body-color: #000;
  --gray-bg: #E5E7E9;
  --footer-bg: #272727;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #999999;
  --h1toh6:#232323;
  --line-light: #D9D9D9;
  --btn-hover: red;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-eb: 800;
  --font-black: 900;
  --font-eblack: 950;
}

body,
html {
  font-family: "Futura";
  line-height: 1.5;
  font-size: 16px;
  color: var(--body-color);
}

* {
  font-family: "Futura";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-sb);
  font-size: 2.65rem;
}

.h2,
h2 {
  font-weight: var(--font-sb);
  font-size: 1.8rem;
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: 1.5rem;
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: 1.2rem;
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: 1.05rem;
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: 0.8rem;
}

p {
  color: var(--p-color);
  font-weight: var(--font-r);
}

a {
  color: var(--pc);
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: auto;
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

@media (max-width: 1920px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 83.2rem;
  }
}
@media (max-width: 1400px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 1088px;
  }
}
@media (max-width: 1200px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl {
    max-width: 1088px;
  }
}
.pc {
  color: var(--pc);
}

.sc {
  color: var(--sc);
}

/* Variables */
.home {
  position: relative;
  padding-block: 3.05rem;
}
.home .bgimage {
  position: absolute;
  top: 5rem;
  inset: 0;
  background: url("../images/bg.webp") center/cover no-repeat;
  z-index: 0;
}
.home .bgimage .overlay {
  position: absolute;
  inset: 0;
  background: rgba(178, 34, 34, 0.6);
}
.home .container {
  display: flex;
  flex-direction: column;
}
.home .container .subsection .content {
  position: relative;
  z-index: 2;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 7rem;
}
.home .container .subsection .content .div1 {
  max-width: 50%;
  color: #ffffff;
  display: flex;
  flex-direction: column;
}
.home .container .subsection .content .div1 h1 {
  max-width: 44.8rem;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 3.2rem;
  line-height: 1;
  letter-spacing: 0;
  margin-bottom: 1rem;
}
.home .container .subsection .content .div1 p {
  font-size: 0.8rem;
  margin-bottom: 1.5rem;
  line-height: 1.6;
  color: #ffffff;
}
.home .container .subsection .content .div1 .apply {
  max-width: 9.1rem;
  border-radius: 3rem;
  background: transparent;
  border: 0.05rem solid #ffffff;
  color: #ffffff;
  cursor: pointer;
}
.home .container .subsection .content .div2 {
  display: flex;
  align-items: center;
  max-width: 50%;
  height: 28.9rem;
}
.home .container .subsection .content .div2 #myform {
  background: #ffffff;
  border-radius: 0.6rem;
  display: flex;
  flex-direction: column;
  padding: 0.75rem;
}
.home .container .subsection .content .div2 #myform h2 {
  text-align: center;
  margin-bottom: 0.25rem;
}
.home .container .subsection .content .div2 #myform h4 {
  text-align: center;
  font-size: 0.7rem;
  margin-bottom: 1rem;
  color: #666;
}
.home .container .subsection .content .div2 #myform label {
  font-size: 0.6rem;
  margin-bottom: 0.2rem;
}
.home .container .subsection .content .div2 #myform input {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 0.75rem;
  border: 0.05rem solid #ccc;
  border-radius: 0.3rem;
}
.home .container .subsection .content .div2 #myform .row {
  display: flex;
  gap: 0.6rem;
}
.home .container .subsection .content .div2 #myform .row .field {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.home .container .subsection .content .div2 #myform .btn {
  background: #b22222;
  color: #ffffff;
  border: none;
  padding: 0.6rem;
  border-radius: 1.5rem;
  cursor: pointer;
  margin-top: 0.5rem;
}

.page1 {
  margin-block: 6rem;
}
.page1 .container {
  display: flex;
  flex-direction: column;
}
.page1 .container h6 {
  color: var(--pc);
  font-weight: 25rem;
  margin-bottom: 0.8rem;
  line-height: 100%;
  font-size: 0.8rem;
  opacity: 0.5;
}
.page1 .container .line {
  width: 100%;
  height: 0.1rem;
  border: 0.05rem solid #DE3338;
  margin-bottom: 2.5rem;
  opacity: 0.1;
}
.page1 .container .subsection {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 0.8rem;
  padding-top: 4rem;
}
.page1 .container .subsection .left {
  max-width: 50%;
  display: flex;
  flex-direction: column;
}
.page1 .container .subsection .left h2 {
  max-width: 100%;
  font-size: 2.4rem;
  line-height: 100%;
  letter-spacing: 0%;
  font-weight: 600;
}
.page1 .container .subsection .left p {
  max-width: 100%;
  gap: 0.5rem;
  color: #555;
  line-height: 1.5rem;
  font-size: 1rem;
  font-weight: 400;
}
.page1 .container .subsection .right {
  display: flex;
  justify-content: center;
  flex-direction: row;
  gap: 4.5rem;
  margin-top: 2rem;
  max-width: 50%;
}
.page1 .container .subsection .right .box {
  width: 9.65rem;
  height: 5.75rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
}
.page1 .container .subsection .right .box h1 {
  font-size: 3.2rem;
  font-weight: 700;
  line-height: 100%;
  font-style: bold;
}
.page1 .container .subsection .right .box h6 {
  color: #DE3338;
  font-weight: 600;
  font-style: bolder;
  font-size: 1.1rem;
  line-height: 100%;
}
.page1 .container .subsection .right .box:not(:last-child)::after {
  content: "";
  position: absolute;
  right: -1.5rem;
  top: 15%;
  height: 70%;
  width: 0.05rem;
  background-color: #ccc;
}

.page2 {
  background-color: rgba(251, 205, 207, 0.1019607843);
}
.page2 .container {
  padding-block: 2.65rem;
}
.page2 .container h6 {
  color: rgba(226, 30, 36, 0.5490196078);
  font-weight: 25rem;
  margin-bottom: 0.8rem;
  line-height: 100%;
  font-size: 0.8rem;
  opacity: 0.5;
}
.page2 .container .line {
  width: 100%;
  height: 0.1rem;
  border: 0.05rem solid #DE3338;
  margin-bottom: 2.5rem;
  opacity: 0.1;
}
.page2 .container .subsection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2.5rem;
}
.page2 .container .subsection .first {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 1.5rem;
  margin-top: 4rem;
}
.page2 .container .subsection .first h2 {
  max-width: 68.55rem;
  font-weight: 600;
  font-size: 2.4rem;
  line-height: 100%;
  text-align: center;
  padding-bottom: 1.5rem;
}
.page2 .container .subsection .first p {
  color: #2b2a2a;
  max-width: 35.15rem;
  font-weight: 25rem;
  font-size: 1.1rem;
  line-height: 1.5rem;
  text-align: center;
}
.page2 .container .subsection .second {
  max-width: 86rem;
  display: flex;
  gap: 1.2rem;
  margin-top: 2.9rem;
}
.page2 .container .subsection .second .box {
  display: flex;
  max-width: 16.25rem;
  align-items: flex-start;
}
.page2 .container .subsection .second .box .left {
  width: 2.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease;
}
.page2 .container .subsection .second .box .left img {
  max-width: 2.75rem;
  height: auto;
  display: block;
}
.page2 .container .subsection .second .box .right {
  flex: 1;
  transition: transform 0.3s ease;
  margin-block: 2rem;
}
.page2 .container .subsection .second .box .right .line {
  width: 100%;
  max-width: 15.25rem;
  height: 0.4rem;
  background: linear-gradient(to right, #FCEBEB 2%, #DF514D 100%);
  border-radius: 0.2rem;
  margin-bottom: 0.6rem;
  border: none;
  opacity: 1;
}
.page2 .container .subsection .second .box .right .content h6 {
  color: #2b2a2a;
}
.page2 .container .subsection .second .box .right .content p {
  opacity: 0;
}
.page2 .container .subsection .second .box:hover .left {
  opacity: 1;
  transform: translateX(0);
}
.page2 .container .subsection .second .box:hover .right {
  transform: translateX(1rem);
}
.page2 .container .subsection .second .box:hover .right p {
  opacity: 1;
}
.page2 .container .subsection .third {
  margin-top: 2.2rem;
  margin-bottom: 2.65rem;
}
.page2 .container .subsection .third .para {
  max-width: 58.3rem;
  font-weight: 20rem;
  font-size: 1rem;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
  color: black;
}

.page3 {
  margin: 6rem 0;
}
.page3 .container .section-tag {
  color: rgba(226, 30, 36, 0.5490196078);
  font-size: 0.7rem;
  font-weight: 500;
  letter-spacing: 0.05rem;
  margin-bottom: 0.5rem;
}
.page3 .container .line {
  max-width: 100%;
  max-height: 0.1rem;
  border: 1px solid #DE3338;
  margin-bottom: 2.5rem;
  opacity: 0.1;
}
.page3 .container .subsection {
  display: flex;
  align-items: center;
  gap: 1.6rem;
}
.page3 .container .subsection .left {
  flex: 1;
}
.page3 .container .subsection .left img {
  border-radius: 0.3rem;
  width: 35.2rem;
  height: 31.5rem;
  margin-top: 1.2rem;
  opacity: 1;
}
.page3 .container .subsection .right {
  flex: 1;
}
.page3 .container .subsection .right h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
  line-height: 1.3;
}
.page3 .container .subsection .right p {
  font-size: 0.75rem;
  color: #666;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}
.page3 .container .subsection .right .items {
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
}
.page3 .container .subsection .right .items .item {
  display: flex;
  gap: 0.8rem;
  align-items: flex-start;
  padding: 0.7rem 0.9rem;
  background: #fff;
  border-radius: 0.4rem;
  box-shadow: 0 0.1rem 0.5rem rgba(0, 0, 0, 0.04);
  width: 50%;
}
.page3 .container .subsection .right .items .item img {
  width: 1.8rem;
  height: 1.8rem;
  flex-shrink: 0;
}
.page3 .container .subsection .right .items .item h4 {
  font-size: 0.7rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}
.page3 .container .subsection .right .items .item span {
  font-size: 0.65rem;
  color: #777;
  line-height: 1.4;
}
.page3 .container .subsection .right .items .item:hover {
  box-shadow: -0.1rem 0.1rem 4cap rgba(0, 0, 0, 0.12), 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.12), 0 0.4rem 0.4rem rgba(0, 0, 0, 0.15);
}
.page3 .container .subsection .right .cta {
  margin-top: 1.5rem;
  background: #e71919;
  color: #fff;
  border: none;
  padding: 0.6rem 1.4rem;
  border-radius: 1.5rem;
  cursor: pointer;
  font-size: 0.7rem;
}

.page4 {
  background-color: #f1dadb;
  padding-bottom: 5rem;
}
.page4 .container .section {
  padding-top: 2.8rem;
}
.page4 .container .section .section-tag {
