  left: 50rem;
  top: 50%;
  background-color: #e71919;
  color: #fff;
  border: none;
  padding: 0.5rem 0.7rem;
  cursor: pointer;
  z-index: 10;
}
.page5 .container .subsection .right {
  max-width: 34.1rem;
}
.page5 .container .subsection .right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header {
  height: 5rem;
}
.header .logo-wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 5rem;
}
.header .logo-wrapper img {
  max-width: 13.5rem;
  display: block;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes lineroad {
  to {
    stroke-dashoffset: 0;
  }
}
